.tab-window {
  width: 100%;
  height: auto;
  border-radius: 10px;
  background: white;
  overflow: hidden;
  box-shadow: 0 1px 1px hsl(0deg 0% 0% / 0.075),
    0 2px 2px hsl(0deg 0% 0% / 0.075), 0 4px 4px hsl(0deg 0% 0% / 0.075),
    0 8px 8px hsl(0deg 0% 0% / 0.075), 0 16px 16px hsl(0deg 0% 0% / 0.075);
  display: flex;
  flex-direction: column;
}

nav {
  background: #696c70;
  padding: 0px 5px 0;
  border-radius: 10px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom: 2px solid #eeeeee;
  display: grid;
  grid-template-columns: 1fr 35px;
  max-width: 100%;
  overflow: hidden;
  color: #000;
}

img {
  width: 100%;
  height: auto;
}
.selected {
  background: #ffffff;
}

.inner-tab {
  background: #f203ee;
  background-color: #f203ee;
  margin-left: 5px;
}

.tab-image {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.tab-description {
  padding-top: 1%;
  color: #232760;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
  font-size: 1.5rem; /* Add this line to control the text size */
}

