  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
} */


li button {
  width: 20px;
  height: 20px;
  border: 0;
  background: #fff;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  stroke: #000;
  margin-left: 10px;
  cursor: pointer;
  flex-shrink: 0;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
}


.add-item {
  width: 80%;
  height: 70%;
  background: #eee;
  border-radius: 50%;
  margin-top: 40%;
  margin-bottom: 20%;
  margin-left: 10%;
  border: 0;
  cursor: pointer;
  align-self: center;
}

.add-item:disabled {
  opacity: 0.4;
  cursor: default;
  pointer-events: none;
}

.label {
  font-size: 0.75rem;
  font-weight: bolder;
  color: #353434;
  margin-top: 1%;
}

.tab-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Add media queries for responsiveness */
@media (max-width: 768px) {
  .label {
    font-size: 0.65rem;
  }

  .tab-description {
    font-size: 0.9rem;
  }

  /* li {
    padding: 8px 10px;
  } */

  .close {
    display: flex; /* 削除ボタンが常に表示されるように */
  }
}

@media (max-width: 480px) {
  .label {
    font-size: 0.5rem;
  }

  .tab-description {
    font-size: 0.8rem;
  }
/* 
  li {
    padding: 6px 8px;
  } */


  .close {
    display: flex; /* 削除ボタンが常に表示されるように */
  }
}

@media (max-width: 400px) {
  .tab-description {
    font-size: 0.6rem;
  }

  li .close {
    display: block; /* Ensure the close button is always displayed */
  }

  .inner-tab {
    display: flex;
    justify-content: space-between;
  }
}
